.banner-slider__wrapper .slider-item {
    z-index: -1;
    position: relative;
    background-size: cover;
    padding: 355px 0 304px;
    background-position: center;
}
@media (max-width: 1399px) {
    .banner-slider__wrapper .slider-item {
        padding: 300px 0 260px;
    }
}
@media (max-width: 1199px) {
    .banner-slider__wrapper .slider-item {
        padding: 200px 180px;
    }
}
@media (max-width: 991px) {
    .banner-slider__wrapper .slider-item {
        padding: 170px 140px;
    }
}
@media (max-width: 767px) {
    .banner-slider__wrapper .slider-item {
        padding: 160px 130px;
    }
}
@media (max-width: 575px) {
    .banner-slider__wrapper .slider-item {
        padding: 140px 115px;
    }
}
.banner-slider__wrapper .slider-item:after {
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    content: "";
    height: 100%;
    position: absolute;
    background-image: url('../img/banner/banner-slider-overly.png');
}
.banner-slider__wrapper .slider-item .number {
    right: 50px;
    font-size: 160px;
    position: absolute;
    line-height: 121px;
    font-family: "Pathway Gothic One";
    top: calc(50% - 60.5px);
    -webkit-text-stroke: transparent;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 1px !important;
    -webkit-text-stroke-color: rgba(255, 255, 255, 0.4);
}
@media (max-width: 767px) {
    .banner-slider__wrapper .slider-item .number {
        right: 25px;
        font-size: 130px;
        line-height: 100px;
    }
}